import { Rectangle, Vector, CameraType, NgineMiddlewareCamera } from '@ngine/core';

import * as THREE from 'three';

export class NgineThreeCamera implements NgineMiddlewareCamera {
  public position: Vector = new Vector(0, 0, 0);
  public zoom: number = 1;
  private camera: THREE.OrthographicCamera;

  constructor (public bounds: Rectangle, type: CameraType, position?: Vector) {
    if (type === CameraType.ORTHO) {
      console.log(bounds.height, bounds.y);
      this.camera = new THREE.OrthographicCamera(
        bounds.width / -2 + bounds.x,
        bounds.width / 2  + bounds.x,
        bounds.height / 2 + bounds.y,
        bounds.height / -2 + bounds.y,
      );
    }

    this.zoom = this.camera.zoom;

    if (position) {
      this.setPosition(position);
    }
  }

  updateBounds (bounds: Rectangle) {
    this.camera.left = bounds.width / -2 + bounds.x;
    this.camera.right = bounds.width / 2 + bounds.x;
    this.camera.top = bounds.height / 2 + bounds.y;
    this.camera.bottom = bounds.height / -2 + bounds.y;
    this.camera.updateProjectionMatrix();
  }

  getMiddlewareCamera () {
    return this.camera;
  }

  setPosition (point: Vector) {
    this.bounds = this.bounds.setPosition(point);
    this.position = this.bounds.tl().setZ(point.z);
    this.camera.position.x = this.position.x;
    this.camera.position.y = this.position.y;
    if (this.position.z) this.camera.position.z = this.position.z;
  }

  setZoom (zoom: number) {
    this.camera.zoom = zoom;
    this.camera.updateProjectionMatrix();
    this.zoom = this.camera.zoom;
  }
}

export const THREE_CAMERAS = [ CameraType.ORTHO ];



