import { NgModule } from '@angular/core';
import { NgineRenderMiddleware, CAMERA_CLASS, NgineMiddlewareAssetFactory } from '@ngine/core';

import { NgineThree } from './render';
import { NgineThreeCamera } from './camera';
import { NgineThreeAssetFactory } from './assets';


@NgModule({
  declarations: [],
  exports: [],
  imports: [],
  providers: [
    { provide: NgineRenderMiddleware, useClass: NgineThree },
    { provide: NgineMiddlewareAssetFactory, useClass: NgineThreeAssetFactory },
    { provide: CAMERA_CLASS, useValue: NgineThreeCamera },
  ]
})
export class NgineThreeModule {
  constructor() {}
}



