import {
  ChangeDetectionStrategy,
  AnimationEntryMetadata,
  ViewEncapsulation,
  Provider,
  Type,
  Component,
  HostBinding
} from '@angular/core';

import { SpriteStyle, StyleBinding, getSpriteStyleMap } from '../render';

const STYLE_KEYWORD = 'style';

export function Sprite(config: SpriteConfig, styleKeyword: string = STYLE_KEYWORD) {
  return function (target) {
    // StyleBinding()(target.prototype.constructor, STYLE_KEYWORD);
    // HostBinding(sprite)

    if (!config.template) {
      config.template = '';
    }
    const styleMap = getSpriteStyleMap(styleKeyword);
    const hostifiedStyleMap = {};
    for (let styleKey of Object.keys(styleMap)) {
        hostifiedStyleMap[`[${styleKey}]`] = styleMap[styleKey];
    }
    config.host = Object.assign({}, hostifiedStyleMap, config.host);
    config.providers = [...(config.providers || []), SpriteStyle];

    Component(config)(target);

    return target;
  };
}

export interface SpriteType {
  style: SpriteStyle;
};


export interface SpriteConfig {
  selector?: string;
  inputs?: string[];
  outputs?: string[];
  properties?: string[];
  events?: string[];
  host?: {
      [key: string]: string;
  };
  providers?: any[];
  exportAs?: string;
  moduleId?: string;
  queries?: {
      [key: string]: any;
  };
  viewProviders?: any[];
  changeDetection?: ChangeDetectionStrategy;
  templateUrl?: string;
  template?: string;
  // styleUrls?: string[];
  // styles?: string[];
  animations?: AnimationEntryMetadata[];
  directives?: Array<Type<any> | any[]>;
  pipes?: Array<Type<any> | any[]>;
  encapsulation?: ViewEncapsulation;
  interpolation?: [string, string];
  precompile?: Array<Type<any> | any[]>;
}

interface ISprite {
  style: SpriteStyle;
}



