import { NgModule, RendererFactory2 } from '@angular/core';

import { NgineRendererFactory, SpriteStyle } from './render';
import { NgineRenderMiddleware } from './middleware';

import {
  SoundService,
  AssetService,
  AssetFactoryService,
  KeyService,
  CameraService,
  ClockService,
  PhysicsService,
  AnimationService,
  MouseService,
  ViewportService,
} from './services';
import { GroupComponent } from './components';


export function rendererFactoryFactory (middleware: NgineRenderMiddleware) {
  return new NgineRendererFactory(middleware);
}


@NgModule({
  declarations: [
    GroupComponent,
  ],
  exports: [
    GroupComponent,
  ],
  imports: [],
  schemas: [ ],
  providers: [
    SoundService,
    AssetFactoryService,
    AssetService,
    CameraService,
    KeyService,
    MouseService,
    ClockService,
    SpriteStyle,
    // { provide: SpriteStyle, useFactory: () => new SpriteStyle() },
    PhysicsService,
    AnimationService,
    ViewportService,
    // { provide: RendererFactory2, useClass: NgineRendererFactory },
    {
      provide: RendererFactory2,
      useFactory: rendererFactoryFactory,
      deps: [ NgineRenderMiddleware ]
    },
  ]
})
export class NgineModule {
  constructor() {}
}



