import { Subject, Observable } from 'rxjs/rx';
import { Bounds, Vector } from '../common';


export abstract class NgineMiddlewarePhysics {
  abstract create(bounds: Bounds, options: PhysicsOptions): Body;
  abstract remove(body: Body): void;
  abstract update(): void;
  abstract createCollisionGroup(name?: string): CollisionGroup;
  abstract getCollisionGroup(name: string): CollisionGroup;
  abstract removeCollisionGroup(name: string): void;
}

export abstract class Body {
  public position$: Observable<Vector>;
  abstract getNativeBody(): any;
  abstract setVelocity(velocity: Vector): void;
  abstract translate(translation: Vector): void;
  abstract addCollisionGroup(group: CollisionGroup): void;
  abstract removeCollisionGroup(group: CollisionGroup): void;
}

export abstract class CollisionGroup {
  public collision$: Observable<Body[]> = new Observable<Body[]>();
  // public collisions: NgineMiddlewareCollisionGroup[] = [];
  abstract addBody(body: Body): void;
  abstract getBodies(): Body[];
}

export interface PhysicsOptions {
  drawRotation?: boolean;
  static?: boolean;
  friction?: number;
  inertia?: number;
  frictionAir?: number;
};


