import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/rx';

import { aliases, codes } from './keycode.lib';

/**
 * Contains event streams for key events.
 * `KeyService` is available as an injectable class. It contains public
 * maps whose values are Observables which can be subscribed to.
 *
 * ### Example
 * ```typescript
 * @Component({...})
 * class PlayerComponent {
 *  constuctor (keys: KeyService) {
 *    keys.keyDown('space').subscribe() => {
 *      this.position.y += 20;
 *    });
 *  }
 * }
 */
@Injectable()
export class KeyService {
  /**
   * Map whose values continually emit (at a fixed interval) while a
   * key is held
   */
  public keyActive: Map<string, Observable<KeyboardEvent>> = new Map();
  /**
   * Map whose values emit once when a key is pressed
   */
  public keyDown: Map<string, Observable<KeyboardEvent>> = new Map();
  /**
   * Map whose values emit once when a key is released
   */
  public keyUp: Map<string, Observable<KeyboardEvent>> = new Map();

  private opts: KeyServiceOpts = {
    sendRate: 5 // 60 frames per second
  };

  constructor() {
    this.keyDown.set(
      'any',
      Observable.fromEvent(document, 'keydown')
        .map((e: KeyboardEvent) => e)
    );
    this.keyUp.set(
      'any',
      Observable.fromEvent(document, 'keyup')
        .map((e: KeyboardEvent) => e)
    );

    for (let name of Object.keys(codes)) {
      let code = codes[name];

      this.keyDown.set(
        name,
        Observable.fromEvent(document, 'keydown')
          .filter((e: KeyboardEvent) => e.which === code)
          .map((e: KeyboardEvent) => e)
      );

      this.keyUp.set(
        name,
        Observable.fromEvent(document, 'keyup')
          .filter((e: KeyboardEvent) => e.which === code)
          .map((e: KeyboardEvent) => e)
      );

      this.keyActive.set(
        name,
        this.keyDown.get(name)
          .exhaustMap((e) => Observable.interval(this.opts.sendRate)
            .map((x) => e)
            .takeUntil(this.keyUp.get(name))
          )
      );

    }
  }
}

export interface KeyServiceOpts {
  sendRate?: number;
}



