import { Injectable, Inject, forwardRef } from '@angular/core';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';
import { Http, ResponseContentType } from '@angular/http';
import { Observable } from 'rxjs/rx';

import {
  NgineMiddlewareAssetFactory,
  CachedImage,
  CachedSound,
  CachedData,
  Asset
} from '../middleware';

import { SoundService, Sound } from './sound.service';

/**
 * Loads and caches in game assets (sounds/image).
 * `AssetService` is available as an injectable class. It's 'load' methods
 * return Observables which can be subscribed to.
 *
 * ### Example
 * ```typescript
 * @Component({...})
 * class PlayerComponent {
 *  constructor (assets: AssetService) {
 *    this.assets.loadImages([{ path: 'foo.png' }]).subscribe(([fooAsset]) => {
 *      this.style.image = fooAsset.style;
 *    });
 *  }
 * }
 * ```
 *
 * ### Example
 * ```typescript
 * @Injectable()
 * export class LevelResolver implements Resolve<any> {
 *   constructor (private assets: AssetService) {}
 *
 *   resolve (route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
 *     return this.assets.loadImages([{ path: 'preloaded-asset.png' }]);
 *   }
 * }
 * ```
 */
@Injectable()
export class AssetService {
  private cache = new Map<string, any>();
  private inProgress = new Map<string, Observable<any>>();

  constructor(
    private http: Http,
    private sanitizer: DomSanitizer,
    @Inject(
      forwardRef(() => AssetFactoryService)
    ) private assetFactory: AssetFactoryService,
    // private zone: NgZone,
  ) { }

  /**
   * Fetches the given array of `ImageAsset`s and places them in the asset cache
   */
  loadImages(assets: ImageAsset[], useCache: boolean = true):
    Observable<LoadedImageAsset[]> {
    return Observable
      .from(assets)
      .concatMap((asset) => this
        .loadImage(asset, useCache)
        .map((loadedAsset) => ({ loadedAsset, asset }))
      )
      .map(({ loadedAsset, asset }) => ({
        style: this.sanitizer.bypassSecurityTrustStyle(asset.path),
        data: loadedAsset
      }))
      .toArray();
  }

  /**
   * Fetches the given array of `SoundAsset`s and places them in the asset cache
   */
  loadSounds(assets: SoundAsset[], useCache: boolean = true):
    Observable<Sound[]> {
    return Observable
      .from(assets)
      .concatMap((asset) => this
        .loadSound(asset, useCache)
      )
      .toArray();
  }

  getLoadedImageData(style: string): CachedImage {
    return (this.cache.get(style) as CachedImage);
  }

  getLoadedSoundData(style: string): CachedSound {
    return (this.cache.get(style) as CachedSound);
  }

  pathsToAssets (paths: string[]): Asset[] {
    return paths.map((path) => ({ path }));
  }

  private loadImage(asset: Asset, useCache: boolean): Observable<CachedImage> {
    const cache = this.checkCache(asset, useCache);
    if (cache) return cache;

    const inProgress$ = Observable
      .from(this.http.get(
        asset.path,
        { responseType: ResponseContentType.Blob }
      ))
      .map((res) => res.blob())
      .flatMap((res) => {
        const image = new Image();
        image.src = window.URL.createObjectURL(res);
        return Observable
          .fromEvent(image, 'load')
          .map(() => image);
      })
      .map((image) => this.assetFactory.createImage(asset, image))
      .do((data) => { this.cache.set(asset.path, data); })
      .first()
      .share();

    this.inProgress.set(asset.path, inProgress$);
    return inProgress$;
  }

  private loadSound(asset: Asset, useCache: boolean): Observable<Sound> {
    const cache = this.checkCache(asset, useCache);
    if (cache) return cache;

    const inProgress$ = Observable
      .of(asset.path)
      .concatMap((path) => {
        const sound = new Audio();
        sound.src = path;
        console.log(sound, sound.src, path);
        return Observable
          .fromEvent(sound, 'canplay')
          .map(() => sound);
      })
      .map((sound) => this.assetFactory.createSound(asset, sound))
      .first()
      .share();

    this.inProgress.set(asset.path, inProgress$);
    return inProgress$;
  }

  private checkCache(asset: Asset, useCache: boolean) {
    if (this.cache.has(asset.path) && useCache) {
      return Observable.of(this.cache.get(asset.path));
    }

    if (this.inProgress.has(asset.path)) {
      return this.inProgress.get(asset.path);
    }
  }
}

export interface ImageAsset extends Asset { };
export interface SoundAsset extends Asset { };

export interface LoadedImageAsset {
  style?: SafeStyle;
  data: CachedImage;
}

export class AssetFactoryService {
  constructor(
    @Inject(
      forwardRef(() => NgineMiddlewareAssetFactory)
    ) private factory: NgineMiddlewareAssetFactory,
    @Inject(
      forwardRef(() => SoundService)
    ) private sounds: SoundService,
  ) { }

  createImage(asset: Asset, image: HTMLImageElement): CachedImage {
    return this.factory.createImage(asset, image);
  }

  createSound(asset: Asset, sound: HTMLAudioElement): Sound {
    return this.sounds.create(asset, sound);
  }
}



