import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/rx';

import { Asset, CachedSound } from '../middleware';

@Injectable()
export class SoundService {
  public volume: number;
  public pan: number;
  public root: Mixer;
  public children: SoundNode[];

  private sounds: Sound[];
  private panNode: StereoPannerNode;
  private gainNode: GainNode;
  private rootContext: AudioContext;

  constructor() {
    this.initMaster();
  }

  initMaster() {
    this.rootContext = new AudioContext();
    // this.root = new Mixer(this.rootContext.destination, this.rootContext);
  }

  create(asset: Asset, sound: HTMLAudioElement): Sound {
    return new Sound(
      sound,
      this.rootContext.createMediaElementSource(sound),
      this.rootContext
    );
  }

  playAll() {
    this.doAll((s) => s.play());
  }

  pauseAll() {
    this.doAll((s) => s.pause());
  }

  stopAll() {
    this.doAll((s) => s.stop());
  }

  private doAll(callback: AllActionCallback) {
    const queue: SoundNode[] = [];
    queue.push(this.root);
    do {
      const soundNode = queue.pop();
      if (soundNode instanceof Sound) {
        callback(soundNode);
      } else if (soundNode instanceof Mixer) {
        soundNode.children.forEach((x) => queue.push(x));
      }
    } while (queue.length !== 0);
  }
}

interface AllActionCallback { (sound: Sound): void; };

export interface SoundNodeOptions {
  volume?: number;
  pan?: number;
}

export interface SoundOptions extends SoundNodeOptions {
  time?: number;
}

export class SoundNode {
  public volume: number;
  public pan: number;

  protected panNode: StereoPannerNode;
  protected gainNode: GainNode;

  constructor(
    protected source: AudioNode,
    protected context: AudioContext,
    opts?: SoundNodeOptions
  ) {
    if (opts) {
      Object.assign(this, opts);
    }

    this.initSound();
  }

  initSound() {
    // this.root.connect(this.context.destination);

    this.panNode = this.context.createStereoPanner();
    this.gainNode = this.context.createGain();

    this.panNode.pan.value = 0;
    this.gainNode.gain.value = 0.5;

    this.source.connect(this.panNode);
    this.panNode.connect(this.gainNode);
    this.gainNode.connect(this.context.destination);
    // this.gainNode.connect(this.root);
  }

  setVolume(volume: number) {
    if (volume < 0 || volume > 1) {
      throw new Error('Invalid volume value');
    }
    this.volume = volume;
    this.gainNode.gain.value = volume;
  }

  setPan(pan: number) {
    if (pan < -1 || pan > 1) {
      throw new Error('Invalid pan value');
    }
    this.pan = pan;
    this.panNode.pan.value = pan;
  }

}

export class Mixer extends SoundNode {
  public children: SoundNode[];

  constructor(
    protected root: AudioNode,
    protected context,
    opts?: SoundNodeOptions
  ) {
    super(root, context, opts);
    if (opts) {
      Object.assign(this, opts);
    }
  }

  addChild(sound: SoundNode) {
    this.children.push(sound);
  }

  removeChild(sound: SoundNode) {
    this.children = this.children.filter((x) => x !== sound);
  }
}

export class Sound extends SoundNode {
  public playing: boolean;
  public ended$: Observable<Sound>;
  public time$: Observable<number>;

  constructor(
    protected element: HTMLAudioElement,
    source: MediaElementAudioSourceNode,
    context: AudioContext,
    opts: SoundOptions = {}
  ) {
    super(source, context, opts);
    if (opts) {
      Object.assign(this, opts);
    }

    this.ended$ = Observable
      .fromEvent(this.element, 'ended')
      .map(() => this);

    this.time$ = Observable
      .fromEvent(this.element, 'timeupdate')
      .map(() => this.element.currentTime);

    // this.connectSound();
  }

  connectSound() {
    const source = this.context.createMediaElementSource(this.element);
    source.connect(this.panNode);
  }

  play() {
    this.element.play();
  }

  pause() {
    this.element.pause();
  }

  stop() {
    this.pause();
    this.setTime(0);
  }

  setTime(secs: number) {
    if (secs < 0) {
      throw new Error('Invalid time value');
    }
    this.element.currentTime = secs;
  }
}



