import { Style, SpriteStyle, GameObjectType } from './styles';
import { NgineRenderMiddleware, NgineMiddlewareSprite } from '../middleware';
import { Vector } from '../common';

export class NgineElement {
  private type: GameObjectType = 'group';

  private sprite: NgineMiddlewareSprite;
  private spriteStyles: SpriteStyle;

  private children: NgineElement[] = [];

  constructor(
    private nodeName: string,
    private middleware: NgineRenderMiddleware,
    private parent?: NgineElement
  ) {}

  toString() {
    return '<' + this.nodeName + '>';
  }

  setStyle (style: string, val: string) {
    if (val === null || val === undefined) return;

    if (style === 'type') {
      this.type = val as GameObjectType;
      this.setType('sprite');
    }

    if (this.type === 'sprite') {
      const styles = this.spriteStyles;
      const gameObject = this.sprite;
      switch (style) {
        case 'position-x':
          styles.position.x = +val;
          this.updatePosition();
          break;
        case 'position-y':
          styles.position.y = +val;
          this.updatePosition();
          break;
        case 'position-z':
          styles.position.z = +val;
          this.updatePosition();
          break;


        case 'rotation-x':
          styles.rotation.x = +val;
          this.updateRotation();
          break;
        case 'rotation-y':
          styles.rotation.y = +val;
          this.updateRotation();
          break;
        case 'rotation-z':
          styles.rotation.z = +val;
          this.updateRotation();
          break;

        case 'image':
          styles.image = val as string;
          gameObject.setImage(styles.image as string);
          break;

        case 'width':
          styles.scale.x = +val;
          this.updateScale();
          break;
        case 'height':
          styles.scale.y = +val;
          this.updateScale();
          break;
        case 'depth':
          styles.scale.z = +val;
          this.updateScale();
          break;
        default:
          break;
      }
    }
  }

  getScale (): Vector {
    if (this.spriteStyles) {
      return this.spriteStyles.scale;
    }
    return new Vector(1, 1, 1);
  }

  getPosition (): Vector {
    if (this.spriteStyles) {
      return this.spriteStyles.position;
    }
    return new Vector(0, 0, 0);
  }

  getRotation (): Vector {
    if (this.spriteStyles) {
      return this.spriteStyles.rotation;
    }
    return new Vector(0, 0, 0);
  }

  updatePosition () {
    if (this.sprite) {
      if (this.parent) {
        this.sprite.setPosition(this.getPosition(), this.parent.getPosition());
      } else {
        this.sprite.setPosition(this.getPosition());
      }

      this.children.forEach((child) => {
        child.updatePosition();
      });
    }
  }

  updateRotation () {
    if (this.sprite) {
      if (this.parent) {
        this.sprite.setRotation(this.getRotation(), this.parent.getRotation());
      } else {
        this.sprite.setRotation(this.getRotation());
      }

      this.children.forEach((child) => {
        child.updateRotation();
      });
    }
  }

  updateScale () {
    if (this.sprite) {
      if (this.parent) {
        this.sprite.setScale(this.getScale(), this.parent.getScale());
      } else {
        this.sprite.setScale(this.getScale());
      }

      this.children.forEach((child) => {
        child.updateScale();
      });
    }
  }

  setParent (parent: NgineElement) {
    this.parent = parent;
  }

  remove () {
    this.setParent(null);
    this.middleware.destroySprite(this.sprite);
  }

  appendChild (child: NgineElement) {
    console.log('apppppend');
    child.setParent(this);
    this.children.push(child);
  }

  removeChild (child: NgineElement) {
    child.remove();
    // const match = this.children.filter((x) => x === child);
    // console.log('yoooo', this.children, child);
    // if (match.length === 1) {
    //   match[0].remove();
    // }

    // this.children = this.children.filter((x) => x !== child);
  }

  setType (type: GameObjectType) {
    if (type === 'sprite') {
      this.sprite = this.middleware.createSprite();
      this.spriteStyles = new SpriteStyle();
    }
  }
};



