import { NgineMiddlewareScene } from '@ngine/core';
import * as THREE from 'three';

export class NgineThreeScene implements NgineMiddlewareScene {
  private scene: THREE.Scene = new THREE.Scene();

  getMiddlewareScene () {
    return this.scene;
  }
}



