import { NgineMiddlewareAssetFactory, CachedImage, Asset, CachedSound } from '@ngine/core';
import * as THREE from 'three';

export class NgineThreeAssetFactory implements NgineMiddlewareAssetFactory {
  createImage (asset: Asset, image: HTMLImageElement): CachedImage {
    return new NgineThreeImageAsset(asset, image);
  }
}

export class NgineThreeImageAsset implements CachedImage {
  public texture;
  public material;

  constructor (asset: Asset, public image: HTMLImageElement) {
    this.texture = new THREE.Texture(image);
    this.texture.magFilter = THREE.NearestFilter;
    this.texture.minFilter = THREE.NearestFilter;
    this.texture.needsUpdate = true;

    this.material = new THREE.SpriteMaterial({
      map: this.texture,
      color: 0xffffff
    });
  }
}



