import { Vector } from '../common';

export abstract class NgineRenderMiddleware {
  abstract createSprite(): NgineMiddlewareSprite;
  abstract destroySprite(sprite: NgineMiddlewareSprite);
  abstract createScene(): NgineMiddlewareScene;
}

export interface NgineMiddlewareSprite {
  setPosition(position: Vector, parentPosition?: Vector);
  setRotation(position: Vector, parentRotation?: Vector);
  setImage(image: string);
  setScale(scale: Vector, parentScale?: Vector);
}

export interface NgineMiddlewareScene {}



