import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs/rx';

/**
 * Creates frame-based animations.
 * `AnimationService` is available as an injectable class.
 *
 * ### Example
 * ```typescript
 * @Component({...})
 * class PlayerComponent {
 *  constructor (assets: AssetService, animation: AnimationService) {
 *    this.assets.loadImages(myFramePaths).subscribe((frames) => {
 *      const walkCycle = animation.interpolate(
 *        animation.weightEqually(frames),
 *        1000
 *      );
 *      walkCycle.play();
 *      walkCycle.frame$.subscribe((frame) => {
 *        this.style.image = frame.style;
 *      });
 *    });
 *  }
 * }
 * ```
 */
@Injectable()
export class AnimationService {
  constructor () {}

  /**
   * Takes the given {@link Weight} objects and a millisecond duration and
   * returns an {@link Animation} class which can be used to play over those
   * objects. Optionally `loop`.
   */
  interpolate<T> (
    items: Weight<T>[],
    duration: number,
    loop: boolean = false
  ): Animation<T> {
    return new Animation(items, duration, loop);
  }

  /**
   * Helper method to create {@link Weight} objects for the given `items` which
   * are weighted equally.
   */
  weightEqually<T> (items: T[]): Weight<T>[] {
    return items.map((item) => ({
      value: item,
      weight: 1 / items.length
    }));
  }
}

/**
 * Controls a frame-based animation.
 */
export class Animation<T> {
  /**
   * Subscribe to this Observable to recieve events whenever a frame should
   * be displayed.
   */
  public frame$: Observable<T>;

  private pause$ = new Subject<boolean>();
  private stop$ = new Subject<boolean>();
  private startFrame$ = new Subject<number>();

  constructor (
    private items: Weight<T>[],
    private duration: number,
    private loop: boolean
  ) {
    const frameTimingsFactory = (startFrame) => {
      return Observable
        .from(this.items)
        .skip(startFrame)
        .concatMap((item) => {
          return Observable
            .interval(item.weight * this.duration)
            .first()
            .map(() => item.value);
        });
    };

    const loopFrameTimings = this.startFrame$.startWith(0)
      .switchMap((startFrame) => frameTimingsFactory(startFrame).concat(
        frameTimingsFactory(0).repeat()
      ));

    this.frame$ = this.pause$
      .switchMap((isPaused) => isPaused ? Observable.never() : loopFrameTimings);
  }

  /**
   * Tell `frame$` to begin emitting frames.
   */
  play () {
    this.pause$.next(false);
  }

  /**
   * Tell `frame$` to stop emitting frames (does not change the current frame).
   */
  pause () {
    this.pause$.next(true);
  }

  /**
   * Tell `frame$` to stop emitting frames and reset the current frame to 0.
   */
  stop () {
    this.stop$.next(true);
    this.startFrame$.next(0);
    this.pause$.next(true);
  }

  /**
   * Tell `frame$` to jump to the given `frame` number.
   */
  setFrame (frame: number) {
    this.startFrame$.next(frame);
  }
}

/**
 * Determines how frames will be weighted over the duration in
 * {@link AnimationService}'s `interpolate`. Generic.
 */
export interface Weight<T> {
  value: T;
  weight: number;
}



