export interface NgineMiddlewareAssetFactory {
  createSound?(asset: Asset, sound: HTMLAudioElement): CachedSound;
}

export abstract class NgineMiddlewareAssetFactory {
  abstract createImage(asset: Asset, image: HTMLImageElement): CachedImage;
}

export interface Asset {
  path: string;
};

export interface CachedImage {
  texture: any;
  material: any;
  image: any;
}

export interface CachedSound {
  sound: any;
}

export type CachedData = CachedImage | CachedSound;



