import { Injectable, Inject } from '@angular/core';
import { Rectangle, Vector, CameraType } from '../common';
import { ViewportService } from './viewport.service';
import { NgineRenderMiddleware, NgineMiddlewareCamera, CAMERA_CLASS } from '../middleware';

/**
 * Manages the cameras for the game.
 * `CameraService` is available as an injectable class.
 *
 * ### Example
 * ```typescript
 * @Component({...})
 * class PlayerComponent {
 *  constructor (camera: CameraService) {
 *    this.camera.setPosition(new Vector(5,5));
 *  }
 * }
 * ```
 */
@Injectable()
export class CameraService {
  private active: NgineMiddlewareCamera;
  private cameras: Map<string, Camera> = new Map();

  private cameraClass;

  constructor (
    @Inject(CAMERA_CLASS) cameraClass: NgineMiddlewareCamera,
    private viewport: ViewportService,
  ) {
    this.cameraClass = cameraClass;
    this.create(
      'root',
      new Rectangle(new Vector(0, 0), new Vector(1920, 1080)),
      // new Rectangle(new Vector(0, 0), this.viewport.getSize()),
      CameraType.ORTHO,
      new Vector(0, 0, 10)
    );
  }

  create (
    name: string,
    bounds: Rectangle,
    type: CameraType,
    position?: Vector
  ): NgineMiddlewareCamera {
    const camera = new this.cameraClass(bounds, type, position);
    this.cameras.set(name, camera);
    if (!this.getActive()) {
      this.activate(camera);
    }
    return camera;
  }

  activate(camera: NgineMiddlewareCamera | string) {
    if (camera instanceof this.cameraClass) {
      this.active = camera as NgineMiddlewareCamera;
    } else {
      this.active = this.get(name);
    }
  }

  getActive(): NgineMiddlewareCamera {
    return this.active;
  }

  get (name: string) {
    return this.cameras.get(name);
  }
}

export interface Camera extends NgineMiddlewareCamera {}



