import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/rx';

import { aliases, codes } from './keycode.lib';
import { CameraService } from './camera.service';
import { Vector } from '../common';

/**
 * Creates event streams for mouse events.
 * `MouseService` is available as an injectable class. It's methods
 * return Observables which can be subscribed to.
 *
 * ### Example
 * ```typescript
 * @Component({...})
 * class CursorComponent {
 *  constuctor (mouse: MouseService) {
 *    mouse.mouseMove().subscribe(({x, y}) => {
 *      this.style.position.x = x;
 *      this.style.position.y = y;
 *    });
 *  }
 * }
 */
@Injectable()
export class MouseService {
  constructor(
    private camera: CameraService,
  ) {}

  /**
   * Get an Observable representing mousedown events for the game. If
   * `absolute` is true, return the coordinates relative to the game window.
   * Otherwise, return them relative to the camera.
   */
  mouseDown (absolute: boolean = false): Observable<NgineMouseEvent> {
    return Observable
      .fromEvent(document, 'mousedown')
      .map((e: MouseEvent) => this.vectorify(e, absolute))
      .map(({x, y}) => ({
        x, y, button: NgineMouseButton.Left, ctrlKey: false, shiftKey: false, altKey: false,
      }));
  }

  /**
   * Get an Observable representing mouseup events for the game. If
   * `absolute` is true, return the coordinates relative to the game window.
   * Otherwise, return them relative to the camera.
   */
  mouseUp (absolute: boolean = false): Observable<NgineMouseEvent> {
    return Observable
      .fromEvent(document, 'mouseup')
      .map((e: MouseEvent) => this.vectorify(e, absolute))
      .map(({x, y}) => ({
        x, y, button: NgineMouseButton.Left, ctrlKey: false, shiftKey: false, altKey: false,
      }));
  }

  /**
   * Get an Observable representing mousemove events for the game. If
   * `absolute` is true, return the coordinates relative to the game window.
   * Otherwise, return them relative to the camera.
   */
  mouseMove (absolute: boolean = false): Observable<NgineMouseEvent> {
    return Observable
      .fromEvent(document, 'mousemove')
      .map((e: MouseEvent) => this.vectorify(e, absolute))
      .map(({x, y}) => ({
        x, y,
      }));
  }

  private vectorify (e: MouseEvent, absolute: boolean): Vector {
    let pos = new Vector(e.pageX, e.pageY);
    if (!absolute) {
      const camera = this.camera.getActive();
      const scaledPos = new Vector(
        pos.x / document.documentElement.clientWidth,
        pos.y / document.documentElement.clientHeight
      );
      const relativePos = scaledPos.scale(camera.bounds.size);
      const cameraMiddle = camera.bounds.setPosition(Vector.zero()).center();

      return relativePos.translate(cameraMiddle.scale(-1)).translate(camera.position);
    }
    return pos;
  }
}

/**
 * Contains positioning, button information, and modifiers for mouse events
 */
export interface NgineMouseEvent {
  x: number;
  y: number;
  button?: NgineMouseButton;
  ctrlKey?: boolean;
  shiftKey?: boolean;
  altKey?: boolean;
}

/**
 * Which mouse button was pressed (for a mousedown or mouseup event)
 */
export enum NgineMouseButton {
  Left,
  Right,
  Middle
};



