import 'reflect-metadata';
import * as getParameterNames from 'get-parameter-names';
import * as objectPath from 'object-path';

import { Injectable, HostBinding } from '@angular/core';
import { Vector } from '../common';
import { SafeStyle } from '@angular/platform-browser';

@Injectable()
export class Style {
  public position = new Vector(0, 0, 0);
  public image: string;
  public type: string = 'group';
}

@Injectable()
export class SpriteStyle {
  public position = new Vector(0, 0, 0);
  public rotation = new Vector(0, 0, 0);
  public scale = new Vector(1, 1, 1);
  public image: SafeStyle;

  // public width: number;
  // public height: number;
  // public depth: number;

  private type: string = 'sprite';

  constructor () {}
}

let count = 0;

export function StyleBinding (): any {
  return function ParamDecorator(target: any, key: string | symbol, parameterIndex: number) {
    const types = Reflect.getMetadata('design:paramtypes', target);
    if (types[parameterIndex] !== SpriteStyle) {
      console.log(types[parameterIndex], SpriteStyle);
      throw Error('That\'s not a SpriteStyle parameter');
    }

    const names = getParameterNames(target);
    const propertyKey = names[parameterIndex];
    const styleMap = getSpriteStyleMap(propertyKey);

    for (let styleKey of Object.keys(styleMap)) {
      const getter = function () {
        return objectPath.get(this, styleMap[styleKey]);
      };

      count++;
      Object.defineProperty(target.prototype, 'test' + count, {
        get: getter,
      });
      HostBinding(styleKey)(target.prototype, 'test' + count);
    }
  };
};




export type GameObjectType = 'sprite' | 'group';

export function getSpriteStyleMap (name: string) {
  return {
    'style.type': `${name}.type`,
    'style.position-x': `${name}.position.x`,
    'style.position-y': `${name}.position.y`,
    'style.position-z': `${name}.position.z`,

    'style.rotation-x': `${name}.rotation.x`,
    'style.rotation-y': `${name}.rotation.y`,
    'style.rotation-z': `${name}.rotation.z`,

    'style.image': `${name}.image`,
    'style.width': `${name}.scale.x`,
    'style.height': `${name}.scale.y`,
    'style.depth': `${name}.scale.z`,
  };
};


