import { Injectable, NgZone } from '@angular/core';

import {
  Rectangle,
  CameraService,
  ClockService,
  ViewportService,
  Vector,
  AssetService,
  NgineRenderMiddleware
} from '@ngine/core';

import { NgineThreeScene } from './scene';
import { NgineThreeSprite } from './sprite';

import * as THREE from 'three';

@Injectable()
export class NgineThree implements NgineRenderMiddleware {
  private renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({
    antialias: false
  });
  private scene: THREE.Scene = new THREE.Scene();
  private meter;

  constructor (
    private camera: CameraService,
    private viewport: ViewportService,
    private clock: ClockService,
    private assets: AssetService,
    private zone: NgZone,
  ) {
    this.renderer.setPixelRatio( window.devicePixelRatio );
    this.renderer.setSize( this.viewport.getWidth(), this.viewport.getHeight() );
    document.body.appendChild( this.renderer.domElement );
    this.animate();

    window.addEventListener('resize', () => this.onWindowResize(), false);
    this.onWindowResize();
  }

  createSprite () {
    return new NgineThreeSprite(this.scene, this.assets);
  }

  destroySprite (sprite: NgineThreeSprite) {
    // console.log('got ittt');
    this.scene.remove(sprite.getMiddlewareSprite());
  }

  createScene () {
    return new NgineThreeScene();
  }

  animate () {
    this.zone.runOutsideAngular(() => {
      // this.meter.tick();
      window.requestAnimationFrame(() => this.animate());
      this.render(this.scene, this.camera.getActive().getMiddlewareCamera());
      this.zone.run(() => {
        this.clock.tick.next();
      });
    });
  }

  render (scene: THREE.Scene, camera: THREE.Camera) {
    this.renderer.clear();
    this.renderer.render(scene, camera);
  }

  private onWindowResize () {
    this.camera.getActive().updateBounds(new Rectangle(
      new Vector(0, 0),
      // new Vector(this.viewport.getWidth(), 1080)
      this.viewport.getSize(),
    ));

    this.renderer.setSize(this.viewport.getWidth(), this.viewport.getHeight());
  }
}



