import { OpaqueToken } from '@angular/core';
import { Vector, Rectangle } from '../common';

export interface NgineMiddlewareCamera {
  position: Vector;
  bounds: Rectangle;
  zoom: number;
  setPosition(point: Vector);
  updateBounds(bounds: Rectangle);
  getMiddlewareCamera();
  setZoom(zoom: number);
}

export let CAMERA_CLASS = new OpaqueToken('ngine.cameraClass');



