import {
  NgineMiddlewareSprite,
  Asset,
  AssetService,
  Vector,
} from '@ngine/core';
import { Subject } from 'rxjs/rx';

import * as THREE from 'three';

export class NgineThreeSprite implements NgineMiddlewareSprite {
  public position: Vector = new Vector(0, 0, 0);
  public rotation: Vector;
  private sprite: THREE.Sprite;
  private scale: Vector = new Vector(1, 1, 1);
  private absolute: boolean = false;

  private imageWidth: number;
  private imageHeight: number;
  private imageDepth: number;

  constructor (private scene: THREE.Scene, private assets: AssetService) {}

  setPosition (point: Vector, parentPosition?: Vector) {
    if (!this.absolute && parentPosition) {
      point = point.translate(parentPosition);
    }
    this.position = point;

    if (this.sprite) {
      this.sprite.position.set(point.x, point.y, point.z);
    }
  }

  setRotation (point: Vector, parentRotation?: Vector) {
    if (!this.absolute && parentRotation) {
      point = point.translate(parentRotation);
    }
    this.rotation = point;

    if (this.sprite) {
      this.sprite.material.rotation = point.y;
      // this.sprite.rotation.set(point.x, point.y, point.z);
    }
  }

  setImage (data: string) {
    // console.log(data);
    const material = this.assets.getLoadedImageData(data).material;

    this.imageWidth = material.map.image.width;
    this.imageHeight = material.map.image.height;
    this.imageDepth = 1;

    if (!this.sprite) {
      this.sprite = new THREE.Sprite(material);
      this.scene.add(this.sprite);
    } else {
      this.sprite.material = material;
    }

    this.sprite.scale.set(this.imageWidth, this.imageHeight, 1);

    this.setPosition(this.position);
    this.setScale(this.scale);
  }

  getMiddlewareSprite (): THREE.Sprite {
    return this.sprite;
  }

  setScale(point: Vector, parentScale?: Vector) {
    if (!this.absolute && parentScale) {
      point = point.scale(parentScale);
    }
    this.scale = point;

    if (this.sprite) {
      this.sprite.scale.set(
        this.imageWidth * this.scale.x,
        this.imageHeight * this.scale.y,
        this.imageDepth * this.scale.z
      );
    }
  }
}



